export default {
	data() {
		return {
			id:0,
			reason:"",
			loading:false
		}
	},
	onLoad(e) {
		if(!e.detail){
			return uni.showToast({
				title: '非法参数',
				icon: 'none'
			});
		}
		this.id = (JSON.parse(e.detail)).id
	},
	methods: {
		submit(){
			this.loading = true
			this.$H.post('/order/'+this.id+'/apply_refund',{
				reason:this.reason
			},{
				token:true
			}).then(res=>{
				this.loading = false
				uni.navigateBack({
					delta: 1
				});
				uni.showToast({
					title: '申请成功，等待商家审核',
					icon: 'none'
				});
			}).catch(err=>{
				this.loading = false
			})
		}
	}
}
